import { mapState } from 'vuex';
import avatarImage from '~/assets/img/avatar.jpg';

export default {
  data() {
    return { avatarImage };
  },
  computed: {
    ...mapState({
      tags: (state) => state.tag.allTags
    })
  },
  methods: {
    viewTag(tag) {
      this.$router.push(`/portal/tag/${tag._id}`);
    }
  }
};
